.tb_rv_container{
  padding: 10px;
}
.tb_rv_wrapper{
  width: 100%;
  background-color: rgb(250, 250, 250);
}
.tb_rv_filter_wrap{
  display: table;
  width: 100%;
  border-bottom: 2px solid rgba(0,0,0,.1);
}

.tb_rv_scrollable{
  overflow-y: auto;
  
}

.tb_rv_filter_wrap_in{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px;
}

.tb_rv_scrollable::-webkit-scrollbar{
  width: 0;
  height: 0;
}

.tb_fil_soc_btn{
  padding: 10px 18px;
  cursor: pointer;
  position: relative;
  margin: 0 1px;
  height: 40px;
  min-width: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.tb_fil_soc_btn::after{
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  bottom: -2px;
  width: 0;
  height: 2px;
  background-color: #218eff;
  transition: width .3s ease-in-out;
}

.tb_fil_soc_btn:hover::after,
.tb_fil_soc_btn.tb_fil_active::after{
  width: 100%;
}

.tb_review_filter_ico{
  font-size: 18px;
}

.tb_fil_btn_txt{    
  font-weight: 600;
  font-size: 14px;
}

.tb_rv_head_text{
    font-size: 20px;
    font-weight: 600;
    margin: 0;
    padding-bottom:5px;
}

.tb_rv_head_social_text{
  display: inline;
}

.tb_rv_review_count {
  font-size: 18px;
  font-weight: 600;
  margin-right: 10px;
}

.tb_rv_rating_wrap,
.tb_rv_banner_wrap{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.tb_rv_banner_wrap{
  justify-content: space-between;
  padding: 15px 20px;
}
.tb_rv_rview_star{
  margin-right: 10px;
}

.tb_rv_banner_btn{
  background: #218eff;
  color: #fff;
  padding: 10px 20px;
  font-size: 14px;
  transition: opacity .3s ease-in-out;
  cursor: pointer;
}

.tb_rv_banner_btn:hover{
  opacity: .8;
}

.tb_rv_banner_btn_wrap{
  position: relative;
  margin: 8px 0;
}

.tb_rv_dropdown_{
  background-color: #ffffff;
  margin-top: 5px;
  border-radius: 3px;
  min-width: 220px;
  position: absolute;
  text-align: left;
  z-index: 99;
  top: 130%;
  right: 0;
  opacity: 0;
  visibility: hidden;
  box-shadow: 0px 10px 30px 0px rgba(0,0,0,0.1);
  transition: all .3s ease-in-out;
}

.tb_rv_dropdown_.tb_rv_dd_show{
  top: 100%;
  opacity: 1;
  visibility: visible;
}

.tb_rv_dropdown_title {
  font-size: 12px;
  color: #111111;
  padding: 8px 15px;
  margin-top: 10px;
  line-height: 1.5;
}

.tb_rv_dropdown_list {
  display: flex;
  align-items: center;
  white-space: nowrap;
  font-size: 13px;
  font-weight: 500;
  padding: 8px 15px;
  cursor: pointer;
  position: relative;
  transition:  background-color .3s ease-in-out;
}

.tb_rv_dropdown_list:after{
  content: '';
  height: 1px;
  background-color: rgba(0,0,0,.02);
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.tb_rv_dropdown_list:hover{
  background-color: rgba(33,142,255,.08);
}

.tb_rv_dd_ico{
  margin-right: 5px;
  font-size: 16px;
}
@media screen and (max-width: 767px) {
.tb_rv_banner_btn {
  padding: 9px 14px;
  font-size: 12px;
}

.tb_rv_head_text {
  transform: scale(0.8);
  margin-left: -10%;
  padding-bottom: 0px;
}

.tb_rv_review_count {
  font-size: 14px;
  margin-right: 5px;
}
.tb_rv_rview_star {
  margin-right: 5px;
}
.tb_rv_rview_star .tb_avg_rating_ico__ {
  font-size: 12px;
  margin: 1px;
}

.tb_rv_review_total_post{
  font-size: 10px;
}
}
@media screen and (max-width: 480px) {
.tb_rv_banner_wrap {
  padding: 10px 15px;
}
}